import React, { useContext, useEffect, useRef } from "react"
import { useState } from "react"
import Modal from "../../../../features/Modal"
import Textarea from "../../../../features/TextArea"
import { PostContext } from "./createPost"

import { createFFmpeg, fetchFile } from "@ffmpeg/ffmpeg"
const ffmpeg = createFFmpeg({ log: true })

export default function VideoFileViewer() {
    const videoPlayerRef = useRef()
    const { formData, submitForm } = useContext(PostContext)
    const { thumbnails, loading, error } = useGenerateThumbnail(formData.video)

    useEffect(() => {
        if (!videoPlayerRef.current) return

        return () => {
            if (!videoPlayerRef.current) return
            const videoURL = URL.createObjectURL(formData.video)
            const video = videoPlayerRef.current

            if (!video) return

            if (video.querySelector("source")) {
                video.querySelector("source").remove()
            }
            const videoSource = document.createElement("source")
            videoSource.src = videoURL

            video.append(videoSource)
        }
    }, [videoPlayerRef.current])
    return (
        <>
            <Modal
                title="Video details"
                action="next"
                options={{ maxHeight: true, maxWidth: true }}
                callBack={(exit, data_) => {
                    if (exit) {
                        // closePostWindow()
                    }
                }}
                children={
                    <>
                        {loading ? (
                            <p>..loading</p>
                        ) : (
                            <div className="width-100 height-100 d-flex justify-content-center align-items-center">
                                <div className="row ">
                                    <div className="col-7-sm">
                                        <form className="p-2">
                                            <div className="input-field">
                                                <label htmlFor="title">
                                                    Title
                                                </label>
                                                <input
                                                    type="text"
                                                    placeholder="video title"
                                                />
                                            </div>
                                            <div className="input-field">
                                                <label htmlFor="description">
                                                    Description
                                                </label>
                                                <Textarea
                                                    row={4}
                                                    placeholder="tell viewers about your video"
                                                    id="videoDescription"
                                                    onSubmit={() => {}}
                                                    onChange={() => {}}
                                                />
                                            </div>
                                            <div className="input-field">
                                                <label htmlFor="">
                                                    Thumbnail
                                                </label>
                                                <div className="d-flex justify-content-evenly gap-10-px maxheight-100-px">
                                                    <div className="cursor-pointer border border-style-dotted br-sm p-1 d-flex flex-column align-items-center gap-5-px">
                                                        <span className="">
                                                            <svg
                                                                xmlns="http://www.w3.org/2000/svg"
                                                                viewBox="0 0 24 24"
                                                            >
                                                                <path d="M3 4V1h2v3h3v2H5v3H3V6H0V4h3zm3 6V7h3V4h7l1.83 2H21c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H5c-1.1 0-2-.9-2-2V10h3zm7 9c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-3.2-5c0 1.77 1.43 3.2 3.2 3.2s3.2-1.43 3.2-3.2-1.43-3.2-3.2-3.2-3.2 1.43-3.2 3.2z" />
                                                            </svg>
                                                        </span>
                                                        <span className="font-sm">
                                                            upload thumbnail
                                                        </span>
                                                    </div>
                                                    {thumbnails?.map(
                                                        (blob, idx) => {
                                                            return (
                                                                <div
                                                                    className={`${
                                                                        idx ===
                                                                        2
                                                                            ? "border"
                                                                            : ""
                                                                    } d-inline-block br-sm overflow-hidden`}
                                                                >
                                                                    <div
                                                                        className="cursor-pointer height-100"
                                                                        style={{
                                                                            borderWidth:
                                                                                "2px",
                                                                            borderColor:
                                                                                "var(--textColor--)",
                                                                            opacity:
                                                                                ".8",
                                                                        }}
                                                                    >
                                                                        <img
                                                                            width={
                                                                                130
                                                                            }
                                                                            height="100%"
                                                                            src={require("../../../../assets/img/cs-social-images-index.jpeg")}
                                                                            alt="file not found"
                                                                        />
                                                                    </div>
                                                                </div>
                                                            )
                                                        },
                                                    )}
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                    <div className="col-5-sm">
                                        <div className="width-fit-content mt-2 px-__">
                                            <video
                                                className="br-sm"
                                                ref={videoPlayerRef}
                                                width="100%"
                                                height="350"
                                                controls
                                                // autoPlay
                                            >
                                                {/*Your browser does not support the video tag. */}
                                            </video>
                                            <div className="">
                                                <span
                                                    className="btn"
                                                    onClick={submitForm}
                                                >
                                                    Submit post
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )}
                    </>
                }
            />
        </>
    )
}

function useGenerateThumbnail(video) {
    const [thumbnails, setThumbnails] = useState(null)
    const [loading, setLoading] = useState(true)

    async function load() {
        await ffmpeg.load()
        setThumbnails(await generateThumbnails())
    }

    useEffect(() => {
        if (!thumbnails) return
        setLoading(false)
    }, [thumbnails])

    const generateThumbnails = async () => {
        ffmpeg.FS("mkdir", "thumbnail")
        ffmpeg.FS("writeFile", "test.mp4", await fetchFile(video))
        await ffmpeg.run(
            "-i",
            "test.mp4",
            "-vf",
            "fps=1/5,scale=1000:-1",
            "-t",
            "20",

            "thumbnail/preview%d.jpg",
        )
        const data = ffmpeg.FS("readdir", "thumbnail")
        const blobs = []

        data.forEach((f) => {
            if (f.length > 5) {
                const d = ffmpeg.FS("readFile", `thumbnail/${f}`)
                const file = new Blob([d.buffer], { type: "image/jpg" })
                blobs.push(file)
            }
        })
        return blobs
    }

    useEffect(() => {
        if (ffmpeg.isLoaded()) return
        return () => load()
    }, [])

    return { thumbnails, loading }
}
